import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { FichaPage } from './ficha.page';
import { FichasModule } from '../components/ficha/fichas.module';
import { LoadSpinnerModule } from '../components/utils/load-spinner/load-spinner.module';
import { CardModule } from 'primeng/card';
import { CarouselModule } from 'primeng/carousel';
import { ImagemDisplayModule } from '../components/imagem-display/imagem-display.module';
import { FooterModule } from '../components/footer/footer.module';

const routes: Routes = [
  {
    path: '',
    component: FichaPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
    FichasModule,
    LoadSpinnerModule,
    CardModule,
    CarouselModule,
    ImagemDisplayModule,
    FooterModule
  ],
  declarations: [FichaPage]
})
export class FichaPageModule { }
