import { Component, OnInit, Input } from '@angular/core';
import { MesasService } from '../services/mesas/mesas.service';
import { FichaService } from '../services/fichas/ficha.service';
import { StorageService } from '../services/storage-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ficha-page',
  templateUrl: './ficha.page.html',
  styleUrls: ['./ficha.page.scss'],
})
export class FichaPage implements OnInit {

  ficha;
  abaAtual: string = 'info';
  loading: boolean = true;
  selectedCapitulo: boolean = false;
  info: {
    nomeRpg: '',
    descricaoRpg: '',
    historiaRpg: '',
    mestre: {
      apelido: '',
      email: '',
      id: ''
    },
    jogadores: [],
    idMesa: '',
    imgURL: '',
    tipoRPG: '',
  }

  capitulos: [{
    numCapitulo: '',
    titulo: '',
    missoes: [],
    idCapitulo: ''
  }]

  missoes: any;


  constructor(private mesaService: MesasService,
    private fichaService: FichaService,
    private storageService: StorageService,
    private router: Router) { }

  ngOnInit() {
    this.abaAtual = 'info';
    this.loadContent();
  }

  loadContent() {
    this.loading = true;
    const { idMesa } = this.storageService.getSelectedMesa();
    this.mesaService.findMesaInicial2(idMesa)
      .subscribe(res => {
        this.info = res;
        this.loadOthers();
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
          this.router.navigateByUrl('perfil');
        });
  }

  refresh() {
    this.abaAtual = 'info';
    this.missoes = null;
    this.loadContent();
  }

  mudaAba(event) {
    this.selectedCapitulo = false;
    if (event.detail.value == 'info') {
      this.missoes = null;
    }
  }

  getColor(aba) {
    if (aba == this.abaAtual) {
      return 'success';
    } else {
      return "warning";
    }
  }

  getRecompensas(recompensas: []) {
    if (recompensas) {
      const recompensa = recompensas.join(', ')
      return recompensa;
    }

  }

  attMissoes(event) {
    this.selectedCapitulo = true;
    const selected = event.detail.value
    const achado = this.capitulos.find(({ idCapitulo }) => idCapitulo == selected).missoes
    if (achado) {
      this.missoes = achado.filter(missao => {
        if (missao['statusMissao'] == "EM_ANDAMENTO" || missao['statusMissao'] == "COMPLETADA" || missao['statusMissao'] == "FRACASSADA") {
          return missao;
        }
      });

      this.missoes = this.missoes.map(m => {
        if (m.statusMissao == "EM_ANDAMENTO") {
          m.recompensas = ['BLOQUEADO']
        }
        return m;
      });
    } else {
      this.missoes = null;
    }
  }

  getStatusMissao(status) {
    switch (status) {
      case 'INCOMPLETA':
        return 'Incompleta';
        break;

      case 'COMPLETADA':
        return 'Completada';
        break;

      case 'FRACASSADA':
        return 'Fracassada';
        break;

      case 'EM_ANDAMENTO':
        return 'Em andamento';
        break;

      default:
        return 0;
    }
  }

  loadOthers() {
    this.loading = true;
    this.mesaService.findMesa(this.info.idMesa)
      .subscribe(res => {
        this.capitulos = res.sistema.capitulos;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });

    this.fichaService.findFichaByMesaAndJogador(this.storageService.getLocalUserId(), this.info.idMesa)
      .subscribe(res => {
        //console.log('entrou')
        //console.log(this.storageService.getLocalUserId(), this.info.idMesa);
        this.ficha = res.idFicha;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }

}
